"use client";

import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import {
  PieChart,
  Pie,
  Cell,
  ResponsiveContainer,
  Legend,
  Tooltip,
} from "recharts";

const visitData = [
  { name: "Geo Fraud", value: 170, color: "#14b8a6" },
  { name: "Blacklisted Fraud", value: 170, color: "#f97316" },
  { name: "Pop Under", value: 80, color: "#eab308" },
  { name: "Device Spoofing", value: 190, color: "#3b82f6" },
  { name: "VPN Proxy", value: 70, color: "#f59e0b" },
];

const eventData = [
  { name: "Geo Fraud", value: 170, color: "#14b8a6" },
  { name: "Blacklisted Fraud", value: 170, color: "#f97316" },
  { name: "Pop Under", value: 80, color: "#eab308" },
  { name: "Device Spoofing", value: 190, color: "#3b82f6" },
  { name: "VPN Proxy", value: 70, color: "#f59e0b" },
];

export function TrafficDonutCharts() {
  return (
    <div className="grid grid-cols-1 md:grid-cols-2 gap-6 mt-6">
      <Card className="bg-gray-950 border-gray-800">
        <CardHeader className="pb-2">
          <CardTitle className="text-lg">Visit Traffic</CardTitle>
        </CardHeader>
        <CardContent className="flex flex-col items-center">
          <div className="h-[200px] w-[200px] relative flex items-center justify-center">
            <ResponsiveContainer width="100%" height="100%">
              <PieChart>
                <Pie
                  data={visitData}
                  cx="50%"
                  cy="50%"
                  innerRadius={60}
                  outerRadius={80}
                  paddingAngle={5}
                  dataKey="value"
                >
                  {visitData.map((entry, index) => (
                    <Cell key={`cell-${index}`} fill={entry.color} />
                  ))}
                </Pie>
                <Tooltip
                  contentStyle={{
                    backgroundColor: "#111",
                    border: "1px solid #333",
                    borderRadius: "4px",
                  }}
                  formatter={(value, name) => [`${value}K (20%)`, name]}
                />
              </PieChart>
            </ResponsiveContainer>
            <div className="absolute inset-0 flex items-center justify-center">
              <span className="text-xl font-bold">570K</span>
            </div>
          </div>
          <div className="grid grid-cols-2 gap-2 mt-4 text-xs">
            {visitData.map((item, i) => (
              <div key={i} className="flex items-center gap-1">
                <div
                  className="h-2 w-2 rounded-full"
                  style={{ backgroundColor: item.color }}
                />
                <span>
                  {item.name} - {item.value}K (20%)
                </span>
              </div>
            ))}
          </div>
          <a href="#" className="text-blue-500 text-xs mt-4">
            View all
          </a>
        </CardContent>
      </Card>

      <Card className="bg-gray-950 border-gray-800">
        <CardHeader className="pb-2">
          <CardTitle className="text-lg">Event Traffic</CardTitle>
        </CardHeader>
        <CardContent className="flex flex-col items-center">
          <div className="h-[200px] w-[200px] relative flex items-center justify-center">
            <ResponsiveContainer width="100%" height="100%">
              <PieChart>
                <Pie
                  data={eventData}
                  cx="50%"
                  cy="50%"
                  innerRadius={60}
                  outerRadius={80}
                  paddingAngle={5}
                  dataKey="value"
                >
                  {eventData.map((entry, index) => (
                    <Cell key={`cell-${index}`} fill={entry.color} />
                  ))}
                </Pie>
                <Tooltip
                  contentStyle={{
                    backgroundColor: "#111",
                    border: "1px solid #333",
                    borderRadius: "4px",
                  }}
                  formatter={(value, name) => [`${value}K (20%)`, name]}
                />
              </PieChart>
            </ResponsiveContainer>
            <div className="absolute inset-0 flex items-center justify-center">
              <span className="text-xl font-bold">480K</span>
            </div>
          </div>
          <div className="grid grid-cols-2 gap-2 mt-4 text-xs">
            {eventData.map((item, i) => (
              <div key={i} className="flex items-center gap-1">
                <div
                  className="h-2 w-2 rounded-full"
                  style={{ backgroundColor: item.color }}
                />
                <span>
                  {item.name} - {item.value}K (20%)
                </span>
              </div>
            ))}
          </div>
          <a href="#" className="text-blue-500 text-xs mt-4">
            View all
          </a>
        </CardContent>
      </Card>
    </div>
  );
} 