"use client";

import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  ResponsiveContainer,
} from "recharts";

const data = [
  { date: "Jan 1", valid: 350, invalid: 150, total: 500 },
  { date: "Jan 2", valid: 380, invalid: 140, total: 520 },
  { date: "Jan 3", valid: 370, invalid: 130, total: 500 },
  { date: "Jan 4", valid: 360, invalid: 120, total: 480 },
  { date: "Jan 5", valid: 380, invalid: 140, total: 520 },
  { date: "Jan 6", valid: 340, invalid: 130, total: 470 },
  { date: "Jan 7", valid: 350, invalid: 120, total: 470 },
  { date: "Jan 8", valid: 350, invalid: 120, total: 470 },
  { date: "Jan 9", valid: 340, invalid: 110, total: 450 },
  { date: "Jan 10", valid: 330, invalid: 110, total: 440 },
  { date: "Jan 11", valid: 340, invalid: 110, total: 450 },
  { date: "Jan 12", valid: 350, invalid: 120, total: 470 },
  { date: "Jan 13", valid: 360, invalid: 130, total: 490 },
  { date: "Jan 14", valid: 370, invalid: 140, total: 510 },
  { date: "Jan 15", valid: 390, invalid: 150, total: 540 },
  { date: "Jan 16", valid: 380, invalid: 140, total: 520 },
  { date: "Jan 17", valid: 370, invalid: 130, total: 500 },
  { date: "Jan 18", valid: 380, invalid: 140, total: 520 },
  { date: "Jan 19", valid: 370, invalid: 130, total: 500 },
  { date: "Jan 20", valid: 360, invalid: 140, total: 500 },
];

export function TrafficTrendChart() {
  return (
    <Card className="bg-gray-950 border-gray-800">
      <CardHeader className="pb-2">
        <div className="flex items-center justify-between">
          <CardTitle className="text-lg">Traffic Trend</CardTitle>
          <div className="flex items-center gap-4">
            <div className="flex items-center gap-1 text-xs">
              <div className="h-2 w-2 rounded-full bg-blue-500" />
              <span>Valid</span>
            </div>
            <div className="flex items-center gap-1 text-xs">
              <div className="h-2 w-2 rounded-full bg-red-500" />
              <span>Invalid</span>
            </div>
            <div className="flex items-center gap-1 text-xs">
              <div className="h-2 w-2 rounded-full bg-orange-500" />
              <span>Total %</span>
            </div>
          </div>
        </div>
      </CardHeader>
      <CardContent>
        <div className="h-[300px] w-full">
          <ResponsiveContainer width="100%" height="100%">
            <LineChart
              data={data}
              margin={{
                top: 5,
                right: 30,
                left: 20,
                bottom: 5,
              }}
            >
              <CartesianGrid strokeDasharray="3 3" stroke="#444" />
              <XAxis dataKey="date" tick={{ fontSize: 10 }} stroke="#888" />
              <YAxis tick={{ fontSize: 10 }} stroke="#888" />
              <Tooltip 
                contentStyle={{ 
                  backgroundColor: "#111", 
                  border: "1px solid #333",
                  borderRadius: "4px"
                }} 
              />
              <Legend />
              <Line
                type="monotone"
                dataKey="valid"
                stroke="#3b82f6"
                activeDot={{ r: 8 }}
                strokeWidth={2}
              />
              <Line
                type="monotone"
                dataKey="invalid"
                stroke="#ef4444"
                strokeWidth={2}
              />
              <Line
                type="monotone"
                dataKey="total"
                stroke="#f97316"
                strokeWidth={2}
                strokeDasharray="3 3"
              />
            </LineChart>
          </ResponsiveContainer>
        </div>
      </CardContent>
    </Card>
  );
} 