"use client";

import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Separator } from "@/components/ui/separator";

export function SummaryStats() {
  return (
    <Card className="bg-gray-950 border-gray-800">
      <CardHeader className="pb-2">
        <CardTitle className="text-lg">Total Traffic</CardTitle>
      </CardHeader>
      <CardContent className="space-y-4">
        <div className="flex items-center justify-between">
          <h3 className="text-purple-500 font-semibold">Visit: 570K</h3>
          <h3 className="text-purple-500 font-semibold">Event: 480K</h3>
        </div>
        
        <Separator className="bg-gray-800" />
        
        <div>
          <h3 className="font-semibold text-gray-400 mb-2">Valid Traffic</h3>
          <div className="flex items-center justify-between">
            <p className="text-green-500 font-semibold">Visit: 180K (57%)</p>
            <p className="text-green-500 font-semibold">Event: 150K (48%)</p>
          </div>
        </div>
        
        <Separator className="bg-gray-800" />
        
        <div>
          <h3 className="font-semibold text-gray-400 mb-2">Invalid Traffic</h3>
          <div className="flex items-center justify-between">
            <p className="text-red-500 font-semibold">Visit: 420K (43%)</p>
            <p className="text-red-500 font-semibold">Event: 300K (2%)</p>
          </div>
        </div>
        
        <Separator className="bg-gray-800" />
        
        <div>
          <h3 className="font-semibold text-gray-400 mb-2">Conversion Rate</h3>
          <div className="flex items-center justify-between">
            <p className="text-purple-500 font-semibold">Valid traffic: 2.3%</p>
            <p className="text-purple-500 font-semibold">Invalid traffic: 0.3%</p>
          </div>
        </div>
        
        <Separator className="bg-gray-800" />
        
        <div>
          <h3 className="font-semibold text-gray-400 mb-2">Conv. Rate (Valid to invalid delta)</h3>
          <div>
            <p className="text-green-500 font-semibold">3X</p>
          </div>
        </div>
      </CardContent>
    </Card>
  );
} 